*{
    margin: 0;
    padding: 0;
}

.main{
    width: 100%;
    background: linear-gradient(to top, rgba(0,0,0,0.5)50%,rgba(0,0,0,0.5)50%),url("../images/backwall.jpg");
    background-position: center;
    background-size: cover; 
    height: 109vh;
}

.navbar{
    width: 100%;
    height: 75px;
    margin: auto;
}

.icon{
    width: 200px;
    float: left;
    height: 70px;
}

.logo{
    color: rgb(148, 148, 10);
    font-size: 25px;
    font-family: Arial, Helvetica, sans-serif;
    padding-left: 20px;
    float: left;
    padding-top: 20px;
}

.menu{
    width: 400px;
    float: left;
    height: 70px;
}

ul{
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
}

ul li{
    list-style: none;
    margin-left: 62px;
    margin-top: 27px;
    font-size: 18px;
}

ul li a{
    text-decoration: none;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    transition: 0.4s ease-in-out;
}

ul li a:hover{
    color: rgb(112, 112, 58);
}

.search{
    width: 300px;
    float: left;
    margin-left: 270px;
    display: flex;
}

.srch{
    font-family: 'Times New Roman', Times, serif;
    width: 200px;
    height: 40px;
    background: transparent;
    border: 1px solid rgb(62, 228, 21);
    margin-top: 13px;
    border-right: none;
    color: #fff;
    font-size: 15px;
    float: left;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.btnn{
    width: 100px;
    height: 40px;
    background-color: orange;
    border: 1px solid #ff7200;
    margin-top: 13px;
    color: #fff;
    cursor: pointer;
    font-size: 15px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.btnn:focus{
    outline: none;
}
.srch:focus{
    outline: none;
}
.srch:hover{
   opacity: 0;
    color: black;
}
.btnn:hover{
    background: #805e42;
    color: rgb(41, 37, 33);
    font-weight: bolder;
    transition: 0.5s ease-in-out;
}

.content{
    width: 1200px;
    height: auto;
    margin: auto;
    color: #fff;
    position: relative;
}

.content p{
    padding-left: 20px;
    padding-bottom: 25px;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 1.2px;
    line-height: 30px;
}

.content h1{
    font-family: 'Times New Roman', Times, serif;
    font-size: 50px;
    padding-left: 20px;
    margin-top: 9%;
    letter-spacing: 2px;
}

.content .cn{
    width: 160px;
    height: 40px;
    background-color: orange;
    border: none;
    /* margin-bottom: 10px; */
    margin: 20px;
    font-size: 18px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.5s ease;
}
.content .cn a{
    text-decoration: none;
    color: black;
    transition: 0.3s ease;
}

.cn:hover{
    background-color: #524439;
    font-size: 1.15em;
    color: rgb(44, 32, 32);
}

.content span{
    color: orange;
    font-size: 60px;
}

/* Login */

.form{
    width: 250px;
    height: 380px;
    background: linear-gradient(to top, rgba(0,0,0,0.8)50%,rgba(0,0,0,0.8)50%);
    position: absolute;
    top: -20px;
    left: 870px;
    border-radius: 10px;
    padding: 25px;
}

.form h2{
    width: 220px;
    font-family: sans-serif;
    text-align: center;
    color: rgb(172, 188, 190);
    font-size: 22px;
    background-color: rgb(70, 42, 42);
    border-radius: 10px;
    margin: 2px;
    padding: 8px;
}

.form input{
    width: 240px;
    height: 35px;
    background: transparent;
    border-bottom: 1px solid orange;
    border-top: none;
    border-right: none;
    border-left: none;
    color: white;
    font-size: 15px;
    letter-spacing: 1px;
    margin-top: 30px;
    font-family: sans-serif;
}

.form a{
    text-decoration: none;
}

.form input:focus{
    outline: none;
}

::placeholder{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    padding-left: 10px;
}


.btn{
    width: 240px;
    height: 40px;
    background: #ff7200;
    border: none;
    margin-top: 30px;
    font-size: 18px;
    border-radius: 10px;
    cursor: pointer;

}
.btn:hover{
    background-color: rgb(15, 27, 36);
    color: #ff7200;
    font-weight: bold;
}

.btn a{
    color: white;
}

.form .link{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 17px;
    padding-top: 20px;
    text-align: center;
}
.form .link a{
    text-decoration: none;
    color: orange;
}


.liw{
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
}